import React, { useEffect, useMemo, useState } from 'react';
import Pagination from '@mui/material/Pagination';
import PaginationItem from '@mui/material/PaginationItem';
import FeedbackModal from '../components/feedbackmodal';
import Feedback from '../components/feedback';
import axios from 'axios';

function Reviews({response, base_url}) {
  const [getFeedback, setGetFeedback] = useState([])
  const [page, setPage] = useState(1);
  const feedback = useMemo(()=>getFeedback.filter((item, id)=> Math.ceil((id+1)/3) == page),[page, getFeedback]);
  const handleChange = (event, value) => {
    setPage(value);
};
  useEffect(async() => {
    setGetFeedback((await axios.get(`${base_url}api/feedbacks/`, {withCredentials:true})).data)
  }, [])
  
  return (
      <>
    <div className='review'>
    <FeedbackModal response={response} base_url={base_url}></FeedbackModal>
      <div className='feedback-list'>
          
          {feedback.map(item=> <Feedback item={item}></Feedback> )}
      </div>
    </div>
    
      <div className='review-paginator'>
    <p className=''>Page: {page}</p>
      <Pagination 
      count={Math.ceil(getFeedback.length/3) || 1} 
      page={page} 
      onChange={handleChange} 
      color='primary' 
      renderItem={(item) => (
          <PaginationItem className='review-feedback'
            {...item}
          />
        )}/>
    </div>
    </>
  );

}

export default Reviews;