import { useNavigate } from 'react-router';
import '../App.css';
import Steam from '../images/Group.png'

function Header({logo, login, logout, isLogin, response}) { 
    let navigate = useNavigate();
   return (
    <>
      <div className="header">
        <img className="logo" style={{cursor: 'pointer'}} onClick={()=>navigate('/')} src={logo} alt="description"/>
        <div className="menu">
          <span className="text">Личный кабинет</span>
          <span className="text" onClick={()=>navigate('/reviews/')}>Отзывы</span>
          {isLogin && <span className="text" onClick={()=>navigate('/sell/')}>Продать предметы</span>}
        </div>
        <div className="auth">
          <img alt="description" src={response?.extra_data?.avatar || Steam} className="login-btn"/>
          {!isLogin && <span className="text" onClick={login}>Войти через Steam</span>}
          {isLogin && <span className="nickname">{response?.extra_data?.personaname}</span>}
          {isLogin && <span className="text" onClick={logout}>Выйти</span>}
        </div>
      </div>
    </>
  );
}

export default Header;