import React, { useState } from 'react';

function Feedback({response, item}) {
        const [value, setValue] = useState(0);
        function changeValue(event) {
           setValue(event.target.value);
        }
        const normal_time = new Date(item?.data).toLocaleTimeString();
        const normal_date = new Date(item?.data).toLocaleDateString();
    return (
        <>
            <div className='single-comment'>
                <img className='login-btn' alt="avatar" src={item?.owner?.avatar}/>
                <div className='text-feedback'>{item?.owner?.personaname}</div>
                <div>{normal_date} {normal_time} </div>
                <button onClick={()=>window.open(item?.owner?.profileurl)}>Профиль Steam</button>
                <p className='review-text'>{item?.text}</p>
               
            </div>
    </>
    );
  }

  export default Feedback;